.popup {
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility, opacity, 0.5s linear;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
